html {
  font-size: 100%;
  box-sizing: border-box;
  display: table;
  margin: auto;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--dark-grayish-blue);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 3rem;
  margin-bottom: 0.1875rem;
  color: var(--very-dark-blue);
  font-weight: 900;
}
@media (min-width: 64em) {
  h1 {
    font-size: 3.125rem;
  }
}
@media (max-width: 39.9375em) {
  h1 {
    font-size: 2.1875rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  font-size: 0.9375rem;
}

h2 {
  font-size: 1.125rem;
}

.bottom-section h1 {
  font-size: 1.75rem;
  color: var(--grayish-blue);
}
@media (max-width: 39.9375em) {
  .bottom-section h1 {
    font-size: 1.5rem;
  }
}
.bottom-section h2 {
  font-weight: 800;
  color: var(--very-dark-blue);
}
@media (max-width: 39.9375em) {
  .bottom-section h2 {
    font-size: 0.875rem;
  }
}
@media (max-width: 39.9375em) {
  .bottom-section p {
    font-size: 0.75rem;
  }
}

.aside-section h2 {
  font-size: 2.375rem;
  color: var(--soft-orange);
}
.aside-section h3 {
  font-size: 1.125rem;
  font-weight: 800;
  color: var(--off-white);
}

:root {
  --soft-orange: hsl(35, 77%, 62%);
  --soft-red: hsl(5, 85%, 63%);
  --off-white: hsl(36, 100%, 99%);
  --grayish-blue: hsl(233, 8%, 79%);
  --dark-grayish-blue: hsl(236, 13%, 42%);
  --very-dark-blue: hsl(240, 100%, 5%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

body {
  padding: 0.625rem 0.625rem;
}
@media (min-width: 64em) {
  body {
    padding: 5rem 7.5rem;
    max-width: 105rem;
  }
}

.container {
  margin: 0 auto 0.75rem;
}
@media (min-width: 64em) {
  .container {
    padding: 0;
  }
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
}
.navbar__menu a {
  margin: 0 0.9375rem;
  color: var(--dark-grayish-blue);
  font-size: 0.9375rem;
}
.navbar__menu a:hover {
  color: var(--soft-red);
}
.navbar__menu a:last-child {
  margin-right: 0.0625rem;
}
.navbar__hamburger {
  display: none;
}
@media (max-width: 39.9375em) {
  .navbar__logo {
    width: 3.125rem;
    height: 1.875rem;
  }
  .navbar__menu {
    z-index: 3;
    background-color: #fff;
    position: fixed;
    right: -100%;
    top: 0rem;
    display: flex;
    flex-direction: column;
    width: 70%;
    height: 100%;
    border-radius: 3px;
    text-align: start;
    padding-left: 1.25rem;
    transition: 0.3s;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.35);
  }
  .navbar__menu.active {
    right: 0;
  }
  .navbar__menu .nav-link {
    padding: 0.625rem 0;
    margin: 0;
  }
  .navbar__menu .nav-link:first-child {
    margin-top: 5rem;
  }
  .navbar__hamburger {
    display: block;
    cursor: pointer;
    z-index: 3;
  }
  .navbar__hamburger .bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: #101010;
  }
  .navbar__hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }
  .navbar__hamburger.active .bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }
  .navbar__hamburger.active .bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }
}

@media (max-width: 39.9375em) {
  .background.dark::before {
    background: black;
    content: "";
    opacity: 0.4;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }
}

.main-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 2fr 1fr 0.5fr;
  grid-column-gap: 1.25rem;
  grid-row-gap: 0rem;
}
.main-section__image {
  grid-area: 1/1/2/3;
  width: 100%; /* or any custom size */
  height: 100%;
  object-fit: cover;
}
.main-section__title {
  grid-area: 2/1/4/2;
  margin: 1.25rem 0 0;
}
.main-section__subtitle {
  grid-area: 2/2/3/3;
  margin: 1.25rem 0 0;
}
.main-section__button {
  grid-area: 3/2/4/3;
  display: flex;
  align-self: flex-start;
  align-items: center;
  justify-content: center;
  background-color: var(--soft-red);
  width: 55%;
  height: 60%;
  cursor: pointer;
}
.main-section__button p {
  text-align: center;
  letter-spacing: 0.1875rem;
  text-transform: uppercase;
  font-size: 0.625rem;
  font-weight: 600;
  color: var(--off-white);
}
.main-section__button:hover {
  background-color: var(--very-dark-blue);
}
.main-section__button:hover p {
  font-size: 0.75rem;
}
@media (max-width: 39.9375em) {
  .main-section {
    display: flex;
    flex-direction: column;
    row-gap: 0.625rem;
  }
  .main-section__button {
    margin: 1.25rem 0;
    height: 5.625rem;
    max-width: 50%;
  }
}

.center-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 2fr;
  grid-column-gap: 1.5625rem;
  grid-row-gap: 0px;
  margin-bottom: 2.5rem;
}
.center-container .main-section {
  grid-area: 1/1/2/3;
}
.center-container .aside-section {
  grid-area: 1/3/2/4;
}
@media (max-width: 39.9375em) {
  .center-container {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0;
    grid-row-gap: 1.875rem;
  }
  .center-container .main-section {
    grid-area: 1/1/1/1;
  }
  .center-container .aside-section {
    grid-area: 2/1/2/1;
  }
}

.bottom-section {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 2.1875rem;
  grid-row-gap: 0px;
}
.bottom-section h1 {
  padding-bottom: 0.3125rem;
}
.bottom-section h2 {
  cursor: pointer;
}
.bottom-section h2:hover {
  color: var(--soft-red);
}
.bottom-section__01 {
  grid-area: 1/1/2/2;
}
.bottom-section__02 {
  grid-area: 1/2/2/3;
}
.bottom-section__03 {
  grid-area: 1/3/2/4;
}
.bottom-section__grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0;
  grid-row-gap: 0px;
}
.bottom-section__grid-container img {
  grid-area: 1/1/2/2;
  width: 100%; /* or any custom size */
  height: 100%;
  object-fit: cover;
}
.bottom-section__grid-container div {
  grid-area: 1/2/2/4;
  padding-left: 1.25rem;
}
@media (max-width: 39.9375em) {
  .bottom-section {
    display: flex;
    flex-direction: column;
    row-gap: 1.5625rem;
  }
  .bottom-section div {
    height: 7.5rem;
  }
  .bottom-section img {
    overflow: hidden;
  }
}

.aside-section {
  background-color: var(--very-dark-blue);
  padding: 1.5625rem;
}
.aside-section__subtitle {
  margin-bottom: 0.3125rem;
  cursor: pointer;
}
.aside-section__subtitle:hover {
  color: var(--soft-orange);
}
.aside-section__text {
  margin: 0.5rem 0 1.5rem;
  padding-bottom: 1.25rem;
  border-bottom: 1px solid;
}
.aside-section__text:last-child {
  border-bottom: none;
  padding-bottom: 0;
  margin-bottom: 0.625rem;
}

.footer-section {
  margin-top: 1.25rem;
  padding-top: 1.25rem;
}
.footer-section .author {
  font-weight: 800;
}